// import axios from "@iso/config/axios-config/axios-instance";
import actions from './actions';
import { rtdb } from "../../library/firebase/firebase";



export const getVendors = (token) => {
    return dispatch => {
        dispatch(actions.getVendors());
        let vendors = rtdb.ref(`vendors/HeadQuarters/`);
        vendors.on('value', (snapshot) => {
            dispatch(actions.getVendors());
            console.log(snapshot.val());
            dispatch(actions.getVendorsSuccess(snapshot.val(), actions.GET_VENDORS));
        });
    }
}
