export const itemExistsInAny = (arraysToSearch,searchString) => {
return arraysToSearch.some(element => {
        return ((element.toLowerCase()).includes(searchString.toLowerCase()))
    });
}

export const itemExistsInEvery = (arraysToSearch,searchString) => {
    
    return arraysToSearch.every(element => {
            return ((element.toLowerCase()).includes(searchString.toLowerCase()))
        });
    }